import { Message } from 'element-ui'
// import {showdown} from 'showdown'
// import {TurndownService} from 'turndown'

/** **********************************************************/
/**
 *  
 */
const ECode = {
  // 
  SUCCESS: 'success',
  // 
  ERROR: 'error'
}

/** **********************************************************/

/**
 * 
 */
const FUNCTIONS = {

  /**
   * 
   * @param tags
   * @returns {string}
   */
  tagsToString: tags => {
    let str = ''
    for (let i = 0; i < tags.length; i++) {
      str += tags[i] + ','
    }
    return str.substr(0, str.length - 1)
  },
  /**
   * 
   * @param str
   * @returns {Array}
   */
  stringToTags: str => {
    if (str !== null && str !== '') {
      return str.split(',')
    } else {
      return []
    }
  },
  // 
  splitStr: (str, flagCount) => {
    if (str == null || str == '') {
      return ''
    } else if (str.length > flagCount) {
      return str.substring(0, flagCount) + '...'
    } else {
      return str
    }
  },
  /**
   * 
   * @param text
   */
  copyText: text => {
    const oInput = document.createElement('input')
    oInput.value = text
    document.body.appendChild(oInput)
    oInput.select() // 
    document.execCommand('Copy') // 
    oInput.className = 'oInput'
    oInput.style.display = 'none'
  },
  /**
   * MarkdownHtml
   * @param text
   */
  markdownToHtml: text => {
    let converter = new showdown.Converter()
    return converter.makeHtml(text)
  },
  /**
   * HtmlMarkdown
   * @param text
   */
  htmlToMarkdown: text => {
    var turndownService = new TurndownService()
    return turndownService.turndown(text)
  },
  /**
   * HtmlMarkdown
   * @param title
   * @param text
   */
  htmlToMarkdownFile: (title, text) => {
    title = title || ''

    let turndownService = new TurndownService()

    let markdown = turndownService.turndown(text)

    // blob,file
    let blob = new Blob([markdown])

    let link = document.createElement('a')

    link.href = window.URL.createObjectURL(blob)

    // 
    link.download = title + '.md'

    link.click()
  },
  /**
   * 
   * @type {{success: message.success, warning: message.warning, error: message.error, info: message.info}}
   */
  message: {
    success: function (message) {
      Message({
        showClose: true,
        message: message || '',
        type: 'success'
      })
    },
    warning: function (message) {
      Message({
        showClose: true,
        message: message || '',
        type: 'warning'
      })
    },
    info: function (message) {
      Message({
        showClose: true,
        message: message || ''
      })
    },
    error: function (message) {
      Message({
        showClose: true,
        message: message || '',
        type: 'error'
      })
    }
  }
}

export default {
  ECode,
  FUNCTIONS
}



