/**
 * WebUtil
 */

export function formatData(arr) {
  const params = new URLSearchParams()
  for (var key in arr) {
    params.append(key, arr[key])
  }
  return params
}

/**
 * YYYY-mm-dd HH:MM2019-06-06 19:45
 * dateFormat("YYYY-mm-dd HH:MM", date)
 * @param fmt
 * @param date
 * @returns {*}
 */
export function dateFormat(fmt, date) {
  let ret;
  const opt = {
    "Y+": date.getFullYear().toString(),        // 
    "m+": (date.getMonth() + 1).toString(),     // 
    "d+": date.getDate().toString(),            // 
    "H+": date.getHours().toString(),           // 
    "M+": date.getMinutes().toString(),         // 
    "S+": date.getSeconds().toString()          // 
    // 
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
    };
  };
  return fmt;
}

/**
 * dateTimeStamp100013
 * @param dateTimeStamp
 * @returns {string}
 */
export function timeAgo(dateTimeStamp) {
  let result = "";
  let minute = 1000 * 60;      //
  let hour = minute * 60;
  let day = hour * 24;
  let week = day * 7;
  let month = day * 30;
  let year = day * 365;
  let now = new Date().getTime();   //
  dateTimeStamp = dateTimeStamp.substring(0, 18);
  //'-''/'
  dateTimeStamp = dateTimeStamp.replace(/-/g, '/');

  let timestamp = new Date(dateTimeStamp).getTime();

  let diffValue = now - timestamp;//

  if (diffValue < 0) {
    return result;
  }
  let minC = diffValue / minute;  //
  let hourC = diffValue / hour;
  let dayC = diffValue / day;
  let weekC = diffValue / week;
  let monthC = diffValue / month;
  let yearC = diffValue / year;

  minC = parseInt(minC)
  hourC = parseInt(hourC)
  dayC = parseInt(dayC)
  weekC = parseInt(weekC)
  monthC = parseInt(monthC)
  yearC = parseInt(yearC)

  if (monthC > 12) {
    result = " " + parseInt(yearC) + ""
  } else if (monthC >= 1 && monthC < 12) {
    result = " " + parseInt(monthC) + ""
  } else if (weekC >= 1 && weekC <= 4) {
    result = " " + parseInt(weekC) + ""
  } else if (dayC >= 1 && dayC <= 6) {
    result = " " + parseInt(dayC) + ""
  } else if (hourC >= 1 && hourC <= 23) {
    result = " " + parseInt(hourC) + ""
  } else if (minC >= 1 && minC <= 59) {
    result = " " + parseInt(minC) + ""
  } else if (diffValue >= 0 && diffValue <= minute) {
    result = ""
  }
  return result;
}



